import React, { useState, useRef, useEffect } from 'react';

//leaflet
import { MapContainer, TileLayer, Marker, Popup, useMapEvents } from 'react-leaflet'; //     useMapEvents import
import 'leaflet/dist/leaflet.css'; // Leaflet CSS import
import L from 'leaflet'; // Leaflet   

//HLS
import HLSPlayer from './HLSPlayer';

//
import Layout from "../layout/Layout";
import "../../styles/Home.scss";


//    (Leaflet        )
const markerIcon = new L.Icon({
  iconUrl: 'https://safecity.busan.go.kr/vue/img/gis_picker_cctv_its.9994bd52.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-shadow.png',
  shadowSize: [41, 41]
});


const Home = () => {
  const position = [35.120696, 129.0411816];  //     ( )
  const [markers, setMarkers] = useState([]); //   
  //  CCTV  
  const items = [
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_1.stream/playlist.m3u8", lot: 129.024297, lat: 35.096521 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_2.stream/playlist.m3u8", lot: 129.029273, lat: 35.097858 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_3.stream/playlist.m3u8", lot: 129.035343, lat: 35.097879 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_4.stream/playlist.m3u8", lot: 129.036658, lat: 35.103092 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_5.stream/playlist.m3u8", lot: 129.037914, lat: 35.11186 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_6.stream/playlist.m3u8", lot: 129.039353, lat: 35.114967 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_7.stream/playlist.m3u8", lot: 129.042203, lat: 35.120005 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_8.stream/playlist.m3u8", lot: 129.052554, lat: 35.132734 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_9.stream/playlist.m3u8", lot: 129.059168, lat: 35.141795 },
    { instlPos: "", hlsAddr: "https://its-stream3.busan.go.kr:8443/rtplive/cctv_10.stream/playlist.m3u8", lot: 129.059051, lat: 35.147345 },
    //    
  ];


  //      
  const MapClickHandler = () => {
      useMapEvents({
        click(e) {
          const { lat, lng } = e.latlng;
          setMarkers([...markers, { lat, lng }]); //    
        }
      });
      return null;
    };

  return (
    <Layout>
      <div className="aside">
        <div className="top"></div>
        <div className="main"></div>
        <div className="bottom"></div>

      </div>
      <div className="main-section">
        <div className="controller" >
          
        </div>
       
        {/* Leaflet   */}
        <MapContainer center={position} zoom={14} style={{ height: "100vh", width: "100%" }}>
          <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          />

          
          {/*  CCTV     */}
          {items.map((item, idx) => (
            <Marker key={idx} position={[item.lat, item.lot]} icon={markerIcon}>
              <Popup minWidth={300} maxWidth={400}> {/*    */}
                <div style={{ width: "300px", height: "100%" }}> {/*     */}
                  <h4>{item.instlPos}</h4>
                  <HLSPlayer hlsUrl={item.hlsAddr} />
                </div>
              </Popup>
            </Marker>
          ))}       

        </MapContainer>
        
      </div>
    </Layout>
  );
};

export default Home;


